.todoTabItem {
  display: inline-block;

  input {
    display: none;
  }
}

.todoTabItemTitle {
  display: block;
  padding: 0.25rem 0.5rem;
  margin: 0.5rem 0.25rem;
  border: 2px solid transparent;
  border-radius: 0.25rem;
  background-color: transparent;
  color: white;

  &:hover {
    border: 2px solid white;
    cursor: pointer;
  }
}

.todoTabItemActive {
  background-color: white;
  color: black;
}
