@mixin btn($brColor) {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.2rem;
  border: 1.5px solid $brColor;
  border-radius: 0.25rem;
  background-color: transparent;
}

.box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.25rem;
  background-color: white;
  margin: 0.25rem 0;
  border-radius: 0.25rem;
}

.title {
  input {
    display: none;
  }
}

.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;

  &:hover div {
    border: 1.5px solid #007bff;
    color: #007bff;
  }
}

.checkmark {
  @include btn(#000);
  margin-right: 0.5rem;

  svg {
    visibility: hidden;
  }

  & ~ p {
    text-decoration: none;
  }
}

.checkmarkChecked {
  svg {
    visibility: visible;
  }

  & ~ p {
    text-decoration: line-through;
  }
}

.edit {
  @include btn(#ffc107);
  color: #ffc107;
  margin: 0 0.25rem;
  transition: background-color 0.25s;

  &:hover {
    background-color: #ffc107;
    color: white;
    cursor: pointer;
  }
}

.remove {
  @include btn(#dc3545);
  color: #dc3545;
  transition: background-color 0.25s;

  &:hover {
    background-color: #dc3545;
    color: white;
    cursor: pointer;
  }
}

.formEditTodo {
  width: 100%;
  margin-right: 0.25rem;

  input {
    outline: none;
    width: 100%;
    padding: 0.25rem;
    border: 1.5px solid black;
    border-radius: 0.25rem;
    font-size: 1rem;

    &:focus {
      border: 1.5px solid #007bff;
    }
  }
}
